var that;
import TabMask from '@/components/chunLei-modal/tabMask';
export default {
	data() {
		return {
			textareaValue: '',
			order: '',
			paylist: [
				{
					img: '/static/img/icon/icon-wxpay.png',
					icon: 'Guoicon-weixin',
					color: '#00c800',
					name: '微信支付',
					id: '0',
					type: 'WECHAT_PAY'
				},
				{
					img: '/static/img/icon/icon-wxpay.png',
					icon: 'Guoicon-zhifubao',
					color: '#06b4fd',
					name: '支付宝支付',
					id: '0',
					type: 'ALIPAY'
				},
				{
					img: '/static/img/icon/iocn-lqpay.png',
					name: '零钱支付',
					icon: 'Guoicon-zijin',
					color: '#F2D5A8',
					id: '1',
					money: 0,
					type: 'BALANCE_PAY'
				}
			],
			payaction: -1,
			content: '',
			goods_list: '',
			dzcard: [],
			modal: false,
			customs: [],
			zmoney: 0,
			address: '',
			new_total_price: 0,
			defaultData: {},
			showModel: false
		};
	},
	methods: {
		onConfirm(){
					if (Number(that.money) < Number(that.new_total_price)) {
						that.$alert('您的余额不足!');
						return;
					} else {
						that.zpay();
					} 
		},
		order_ok() {
			if (!that.address) {
				that.$alert('请选择收货地址!');
				return;
			}
			if (that.payaction == -1 && !that.dzpay) {
				that.$alert('请选择支付方式!');
				return;
			}
			var dzmoney = 0;
			var zmoney = that.new_total_price; 
			if (that.paylist[that.payaction].type == 'BALANCE_PAY') { 
				that.showModel=true;
				that.defaultData = {
					title: '提示',
					content:'您的账户余额为：' + that.money + '元,是否确认支付', 
					cancelText: '取消',
					confirmText: '确定',
					confirmColor: '#3CC51F'
				};   
			} else {
				that.zpay();
			}
		},
		zpay() {
			var dzmoney = 0;
			var zmoney = that.new_total_price;
			var goods_info = that.order.goods_info;
			var paytypei = goods_info.type;
			var paytype1 = '';
			if (paytypei == 'ONLY_BUY') {
				paytype1 = 'ONLY_BUY';
			} else {
				paytype1 = goods_info.parent_id != 0 ? 'GROUP_BUY_C' : 'GROUP_BUY';
			}
			goods_info['real_price'] = that.new_total_price;
			goods_info['pay_price'] = dzmoney < zmoney ? (Number(that.new_total_price) - Number(dzmoney)).toFixed(2) : that.new_total_price;
			that.$request.request({
				url: that.api.group.submit,
				data: {
					offline: 1,
					address_id: that.address.id,
					goods_info: JSON.stringify(goods_info),
					content: that.content,
					parent_id: goods_info.parent_id,
					type: paytype1,
					payment: 3,
					formId: ''
				},
				method:"POST",
				success(res) {
					console.log(res);
					console.log('单其他方式支付');
					console.log('支付方式:', that.paylist[that.payaction].type);
					that.$pay.grouppay(that.paylist[that.payaction].type, res.order_id).then(res1 => {
						if (res1.code == 1) {
							console.log('支付失败！');
							that.$alert('支付失败！');
						} else {
							console.log('支付成功！');
							that.$alertok('支付成功！');
							setTimeout(function() {
								if (goods_info.parent_id != 0) {
									uni.redirectTo({
										url: '/pages/order/groupOrder/orderlist/orderlist'
									});
								} else {
									if (paytype1 == 'ONLY_BUY') {
										uni.redirectTo({
											url: '/pages/order/groupOrder/orderlist/orderlist?id=2'
										});
									} else {
										uni.redirectTo({
											url: '/pages/order/groupOrder/gorupgoodsinfo/gorupgoodsinfo?id=' + res.order_id
										});
									}
								}
							}, 1000);
						}
					});
				}
			});
		},
		getconten(e) {
			// console.log(e.detail.value)
			that.content = e.detail.value;
		},
		getpaytype(e) {
			if (that.payaction == e.currentTarget.dataset.index) {
				that.payaction = -1;
			} else {
				that.payaction = e.currentTarget.dataset.index;
			}
			that.$forceUpdate();
		},
		// 控制弹窗
		hideModal(e) {
			that.modal = !that.modal;
		},
		//获取总价
		getprice() {
			var price = 0;
			var mch_list = that.order;
			price = (Number(mch_list.total_price) + Number(mch_list.express_price)).toFixed(2);
			that.new_total_price = price;
			that.$forceUpdate();
		}
	},
	onLoad() {
		that = this;
		var order = uni.getStorageSync('order');
		console.log('订单信息', order);
		that.order = order;
		that.goods_list = order.list;
		that.address = that.order.address;
		// console.log(that.order);
		that.getprice();
		that.money = uni.getStorageSync('money');
	},
	onShow() {
		var address = uni.getStorageSync('address');
		if (address) {
			that.address = address;
		}
		that.$forceUpdate();
	}
};
